.details-list {
	padding: 0 3px;
	&__item {
		position: relative;
		padding-right: 44px;
		@include clr;
		&.is-editing {
			.details-list {
				&__value,
				&__edit {
					display: none; }
				&__btn-icon,
				&__select {
					display: block; } } } }
	&__label {
		float: left;
		color: #97a3b4;
		@include f(13px, null, 1.7); }
	&__value {
		position: relative;
		float: right;
		padding-right: 18px;
		color: #97a3b4;
		@include f(13px, 600, 1.7);
		&:before {
			content: "-";
			position: absolute;
			right: 0;
			top: 0; } }
	&__edit {
		position: absolute;
		top: 0;
		right: 0;
		padding-left: 11px;
		height: 40px;
		color: $iris;
		background: none;
		text-transform: uppercase;
		@include f(13px, 600, normal); }
	&__label,
	&__value,
	&__value:before {
		padding-top: 9px;
		padding-bottom: 9px; }
	&__btn-icon {
		position: absolute;
		right: 0;
		top: 0;
		display: none; }
	&__select {
		margin-right: 8px;
		float: right;
		max-width: 140px;
		display: none;
		.select__field {
			padding-right: 35px; } } }
